@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet{
        flex-direction: row;
        gap: 2rem;
        
     }
    

    &__enlace {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        font-size: 1.4rem;
        @include m.tablet{
            margin-bottom: 0;
            
         }

        &:last-of-type {
            margin-bottom: 0;
        }
        &--crear {
            font-weight: 700;
            color: v.$primario;


           @include m.tablet { 
               &::after {
                 content: ''; 
                 display: inline-block; 
                 height: 4rem;
                 border-right: 2px solid v.$grisClaro;
                 margin: 0 0 0 2rem;
               }
            }        
        }
    }



}


